
<template>
    <div>
      <div id="reg">
        <form id="form-register">
          <div class="m-tab">
	          <div class="left-select"></div>
	        	<div class="right-select"><a href=""></a></div>
	        </div>
          <div class="u-input">
	          <label for="name" class="u-label">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp :&nbsp</label>
		        <input type="text" name="uname"id="uname"placeholder="" minlength="6" maxlength="12"
             class="i-input" v-model="iName"  @blur="vChange" v-on:input="vinp">
		        <span :class="cName?'msg-default':'msg-success'">{{sName}}</span>
	        </div> 
          <div class="u-input">
            <label for="upwd" class="u-label">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp :&nbsp</label>
            <input type="password" name="upwd"  minlength="6" maxlength="16"id="upwd" placeholder="6-16," 
            class="i-input" v-model="ipwd" v-on:input="vpwd">
            <span :class="cpwd?'msg-default':'msg-success'">{{spwd}}</span>
          </div>
          <div class="u-input">
            <label for="upwd2" class="u-label"> :&nbsp</label>
            <input type="password" name="upwd" id="upwd2"  minlength="6" maxlength="16"
            placeholder="" class="i-input" v-model="ipwd2" v-on:input="vpwd2">
            <span :class="cpwd2?'msg-default':'msg-success'">{{spwd2}}</span>
          </div>
          <div class="u-input">
            <label for="phone" class="u-label">&nbsp;&nbsp&nbsp :&nbsp</label>
            <input type="text" name="phone" id="phone" placeholder="11" 
            class="i-input" v-model="iphone" v-on:input="vphone">
            <span :class="cphone?'msg-default':'msg-success'">{{sphone}}</span>
          </div>
          <div class="u-input">
            <label for="email" class="u-label"> :&nbsp</label>
            <input type="email" name="email" id="email" placeholder="" 
            class="i-input" v-model="iemail" v-on:input="vemail">
            <span :class="cemail?'msg-default':'msg-success'">{{semail}}</span>
          </div>
          <p></p>
	        <input type="button" class="b-btn" id="bt-register" value="         " @click="regi">
        </form>
      </div>
    </div>
</template>
<script>
 export default {
  name: 'Register',
  data () {
    return {
      iName: '',
      sName:"",
      cName:1,
      ipwd:"",
      spwd:"",
      cpwd:1,
      ipwd2:"",
      spwd2:"",
      cpwd2:1,
      pattern:/^(13[0-9]{9})|(18[0-9]{9})|(14[0-9]{9})|(17[0-9]{9})|(15[0-9]{9})$/,
      iphone:"",
      sphone:"",
      cphone:1,
      epattern:/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/,
      iemail:"",
      semail:"",
      cemail:1
    }
  },
  methods:{
        //
    vChange:function(){
      if(this.iName.length>=6){
        this.$http.get("http://127.0.0.1/sg/data/log/check_uname.php",{params:{uname:this.iName}})
      .then(function(res){
         if(res.body.code===201&&this.iName.length>=6){
            this.sName="";
            this.cName=true;
         }else if(res.body.code===200&&this.iName.length>=6){
            this.sName="";
            this.cName=false;
         }
      },function(err){
        console.log(err);
      });
    }
    },
    //
    vinp:function(){
      if(this.iName.length==0){
          this.sName="";
          this.cName=true;
        }else if(this.iName.length<6){
          this.sName="6";
          this.cName=true;
        }else{
          this.sName="";
          this.cName=false;
        }
    },
    //
    vpwd:function(){
      if(this.ipwd.length==0){
          this.spwd="";
          this.cpwd=true;
        }else if(this.ipwd.length<6){
          this.spwd="6";
          this.cpwd=true;
        }else{
          this.spwd="";
          this.cpwd=false;
        }
    },
    //
    vpwd2:function(){
       if(this.ipwd2.length==0){
         this.spwd2="";
          this.cpwd2=true;
       }else if(this.ipwd2!=this.ipwd){
          this.spwd2="";
          this.cpwd2=true;
       }else if(this.ipwd2==this.ipwd){
          this.spwd2="";
          this.cpwd2=false;
       }
    },
    //
    vphone:function(){
      if(!this.pattern.test(this.iphone)){
        this.sphone="";
        this.cphone=true;
      }else{
        this.sphone="";
        this.cphone=false;
      }
    },
    //
    vemail:function(){
      if(!this.epattern.test(this.iemail)){
        this.semail="";
        this.cemail=true;
      }else{
        this.semail="";
        this.cemail=false;
      }
    },
    //
    regi:function(){
      if(this.cName==false&&this.cpwd==false&&this.cpwd2==false&&this.cphone==false&&this.cemail==false){
        this.$http.post("http://127.0.0.1/sg/data/log/register.php",
        {headers:{'X-Requested-With': 'XMLHttpRequest'}},
	   {params:{uname:this.iName,upwd:this.ipwd,email:this.iemail,phone:this.iphone}})
      .then(function(res){
         if(res.body.code==200){
            alert("");
            this.$router.push('/Login');
         }
      },function(err){
        console.log(err);
      });
      }else{
        alert('');
      }
    }
  }
}
</script>
<style scoped>
  
</style>



