<template>
    <div>
     <div id="hea">
<!--  -->
				<ul id="headbarnav">
					<li><a href="index.html"></a></li>
					<li><a href="##"></a></li>
					<li><a href="##"></a></li>
					<li><a href="##"></a></li>	
				</ul>
<!--  -->
			<div id="search">
				<strong id="searchbox"></strong>
				<span>|</span>
				<input type="text" class="no1" id="txtsearch" placeholder="" v-model="val">
			
				<router-link :to="'/Product/'+this.val"class="btn-s" id="btn-search" @click="search"><i></i></router-link>
			</div>
<!-- 	-->
			<div id="user_div">
				<div id="shop_cart"></div>
				<div class="headtool" id="btnLogin" :class="hid?'':'hide1'">		
					<router-link :to="'/Login'" id="login"></router-link>
					<router-link :to="'/Register'"></router-link>
				
				</div>
				<div class="headtool" id="welcomes" :class="hid?'hide1':''"  @click="out">
					<span>hi</span>
					<a href="##" id="uname">{{uname}}</a>
					<router-link :to="'/Home'" id="btnLogout"></router-link>
					
				</div>
			</div>
		</div>
		<div id="nav1">
			 <ul id="menunav">
					<li><a href="##"></a></li>
					<li><a href="##"></a></li>
					<li><a href="##"></a></li>
					<li><a href="##"></a></li>
					<li><a href="##"></a></li>		
				</ul>
				<a href="cart.html">
				<div class="shoppingcart">
					 <img src="../assets/img/1/cart.png" alt="">
					 <span></span><sup>0</sup>
					
				</div>
				 </a>
		</div>
    </div>
</template>
<script>
    export default {
  name: 'Header',
  data () {
    return {
      msg: 'Welcome',
			val:"",
	  uname:"",
	  hid:true	
    }
  },created:function(){
	  this.uname=sessionStorage.getItem("name");
	if(this.uname){
		this.hid=false;
	}
  },
	methods:{
		search:function(){
			this.$router.push('/Product/'+this.val);
		},
		out:function(){
			sessionStorage.removeItem("name");
			this.hid=true;
		}
	}
}
</script>
<style>
</style>


